  margin: 2rem auto; }

input {
  text-align: center; }
  input[name="firstName"] {
    text-transform: capitalize; }

[name="lastName"] {
  text-transform: capitalize; }

legend {
  margin: 0 auto; }

label:first-child {
  margin-right: 2rem; }

button {
  background-color: #3A9AFC;
  color: #FFFFFF;
  width: 50%;
  font-weight: bolder;
  cursor: pointer; }

input[type="radio"] {
  cursor: pointer; }

.submit {
  display: block; }

input:focus,
textarea:focus {
  outline: none;
  background-color: #FFEBE6; }

fieldset {
  border: none; }
  fieldset input {
    display: inline; }
